@font-face {
  font-family: "TfArrow";
  src: url("../fonts/tfarrow/tfarrowlight-normal-webfont.woff2") format("woff2"),
    url("../fonts/tfarrow/tfarrowlight-normal-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Gilroy";
  src: url("../fonts/gilroy/Gilroy-Regular.woff2") format("woff2"),
    url("../fonts/gilroy/Gilroy-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

html,
body {
  box-sizing: "border-box";
  padding: 0;
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}
